.container{
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
    
}
.center{
    padding: 15px 20px;
    align-items: center;
}
.container h2{
    font-size: 42px;
    font-weight: 900;
    width: 700px;
    text-align: left;
}
.container li{
    padding: 10px 0;
    font-size: 29px;
    margin: 10px;
    width: 700px;
}
.container p{
    width: 700px;
}